@import "../styles/clickable";

$color-tan: #e5d2c0;
$color-spotify-green: #20af43;

a.noThanks {
  text-decoration: none;
}

.button {
  @extend %clickable;
  border: none;
  text-decoration: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;

  background-color: white;
  color: black;
  font-size: 1.2rem;
  padding: 1rem 1.75rem;
  text-transform: uppercase;
  border-radius: 3rem;
  margin-top: 1.5rem;
  transition: opacity 0.3s, transform 0.3s;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  &.isLoading {
    opacity: 0.8;
    color: rgba(1, 1, 1, 0);
    cursor: default;

    & .listenWords {
      color: rgba(1, 1, 1, 0.5);
    }

    &:hover {
      transform: scale(1);
    }
  }

  &.isDisabled {
    opacity: 0.15;
    // background: rgba(0, 0, 0, 0.3);
    cursor: default;
    &:hover {
      transform: scale(1);
    }
  }

  &:hover {
    background-color: white;
  }
}
